:root {
  --scroll-offset: 0px;
  --animation-left: -100%;
  --animation-right: 100%;
  --animation-top: -100%;
  --animation-bottom: 100%;
  --columns: 12;
  --adaptive-box: 100%;
  --body-font-size: 0.14rem;
  --gap-x: 0.1rem;
  --gap-y: 0.1rem;
  --container-gap: 0.1rem;
}
@media (min-width: 1024px) {
  :root {
    --body-font-size: 0.18rem;
  }
}
@media (min-width: 1024px) {
  :root {
    --gap-x: 0.16rem;
  }
}
@media (min-width: 1024px) {
  :root {
    --gap-y: 0.16rem;
  }
}
@media (min-width: 1024px) {
  :root {
    --container-gap: 0.2rem;
  }
}
body {
  line-height: 1.44;
  font-weight: 400;
  font-size: var(--body-font-size);
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  margin: 0;
  color: #353535;
  background-color: #fafafa;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

main {
  flex-grow: 1;
  padding-top: 1.07rem;
}
@media (min-width: 768px) {
  main {
    padding-top: 0.65rem;
  }
}
@media (min-width: 1024px) {
  main {
    padding-top: 0.7rem;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  text-size-adjust: none;
}

iframe {
  border: 0;
}

button,
input,
optgroup,
select,
textarea {
  outline: none;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  background: transparent;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

input[type=number]:hover,
input[type=number]:focus {
  -moz-appearance: number-input;
}

button {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
}

input::placeholder,
textarea::placeholder {
  opacity: 1;
  color: currentColor;
}

[tabindex="-1"]:focus {
  outline: none !important;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Montserrat", sans-serif;
  line-height: normal;
  font-weight: 700;
  margin-top: 0;
  margin-bottom: 0.1rem;
  color: currentColor;
}

p,
ul,
ol,
dl,
table,
blockquote,
pre,
address,
figure {
  margin-top: 0;
  margin-bottom: 0.1rem;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

li {
  line-height: inherit;
}

address {
  font-style: normal;
}

blockquote {
  margin-right: 0;
  margin-left: 0;
  padding: 0;
}

b,
strong {
  font-weight: 700;
}

a {
  transition-duration: 400ms;
  color: currentColor;
  background-color: transparent;
}

a,
button,
[role=button],
input,
label,
select,
textarea {
  touch-action: manipulation;
  margin: 0;
}

a:not([href]) {
  text-decoration: none;
  color: inherit;
}
a:not([href]):hover, a:not([href]):focus {
  text-decoration: none;
  color: inherit;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

[role=button] {
  cursor: pointer;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

select {
  word-wrap: normal;
}

button,
html [type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

input[type=date],
input[type=time],
input[type=datetime-local],
input[type=month] {
  -webkit-appearance: listbox;
}

input[type=radio],
input[type=checkbox] {
  padding: 0;
  box-sizing: border-box;
}

[hidden] {
  display: none !important;
}

textarea {
  overflow: auto;
  resize: vertical;
}

img {
  max-width: 100%;
  height: auto;
  border-style: none;
}

:root {
  --width-max-window: 100vw;
  --width-min-window: 320;
  --width-min-window-px: 320px;
  --min-value: 100;
  --min-value-px: 100px;
  --max-value: 150;
  --layout-size: 480;
  font-size: calc(var(--min-value-px) + (var(--max-value) - var(--min-value)) * (var(--width-max-window) - var(--width-min-window-px)) / (var(--layout-size) - var(--width-min-window)));
}
@media (min-width: 480px) {
  :root {
    --width-max-window: 640px;
    --width-min-window: 480;
    --width-min-window-px: 480px;
    --layout-size: 840;
  }
}
@media (min-width: 640px) {
  :root {
    --width-max-window: 768px;
    --width-min-window: 640;
    --width-min-window-px: 640px;
    --layout-size: 900;
  }
}
@media (min-width: 768px) {
  :root {
    --width-max-window: 1024px;
    --width-min-window: 768;
    --width-min-window-px: 768px;
    --layout-size: 1400;
  }
}
@media (min-width: 1024px) {
  :root {
    --width-max-window: 100vw;
    --width-min-window: 1024;
    --width-min-window-px: 1024px;
    --min-value: 65;
    --min-value-px: 65px;
    --max-value: 100;
    --layout-size: 1440;
  }
}
@media (min-width: 1440px) {
  :root {
    --width-min-window: 1440;
    --width-min-window-px: 1440px;
    --min-value: 100;
    --min-value-px: 100px;
    --layout-size: 1920;
  }
}
@media (min-width: 1920px) {
  :root {
    --width-max-window: 1920px;
  }
}

.flex,
.grid {
  --grid-gap-x: var(--gap-x);
  --grid-gap-y: var(--gap-y);
  --grid-columns: var(--columns);
}

.container-full, .container\@lg, .container\@md, .container\@xs, .container {
  --local-container-gap: var(--container-gap);
  --dp-block: block;
  display: var(--dp-block);
  width: 100%;
  padding-left: var(--local-container-gap);
  padding-right: var(--local-container-gap);
  margin-left: auto;
  margin-right: auto;
}

@media (min-width: 480px) {
  .container {
    max-width: 440px;
  }
}
@media (min-width: 640px) {
  .container\@xs, .container {
    max-width: 520px;
  }
}
@media (min-width: 768px) {
  .container\@md, .container, .container\@xs {
    max-width: 720px;
  }
}
@media (min-width: 1024px) {
  .container\@lg, .container, .container\@md, .container\@xs {
    max-width: 14.6rem;
  }
}

br {
  --dp-inline: inline;
  display: var(--dp-inline);
}

.icon {
  width: 1em;
  height: 1em;
  --dp-block: block;
  display: var(--dp-block);
  flex: 0 0 auto;
  fill: currentColor;
  transition: all 400ms 0s;
  position: relative;
}
.icon--no-fill {
  fill: none;
}
.icon--stroke {
  stroke: currentColor;
}
.icon--only-stroke {
  fill: none;
  stroke: currentColor;
}

.loader {
  height: 1em;
  width: 1em;
  display: block;
  overflow: hidden;
  position: relative;
}
.loader:after, .loader:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: 3px solid currentColor;
  border-radius: 50%;
}
.loader:after {
  opacity: 0.15;
}
.loader:before {
  border-bottom-color: transparent;
  border-top-color: transparent;
  animation: loading 1.5s linear infinite;
}
.loader--center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
}
.loader--size-sm {
  font-size: 0.4rem;
}
.loader--size-md {
  font-size: 0.6rem;
}
.loader--size-lg {
  font-size: 0.8rem;
}

.adaptive-box {
  position: relative;
  width: 100%;
  --dp-block: block;
  display: var(--dp-block);
}
.adaptive-box:after {
  content: "";
  padding: 0px 0px var(--adaptive-box) 0px;
  width: 100%;
  display: block;
}
.adaptive-box > * {
  position: absolute;
}
.adaptive-box__image, .adaptive-box__item {
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}
.adaptive-box__image {
  object-fit: cover;
}

.link {
  color: currentColor;
  padding: 0;
  border: 0;
  transition: 0.4s;
}
@media (pointer: fine) {
  .link:hover {
    color: #714021;
  }
  .link:active {
    color: #90512a;
  }
}
.link:active {
  color: #90512a;
}
.link-decor {
  color: currentColor;
  position: relative;
  text-decoration: none;
}
.link-decor:before {
  content: "";
  position: absolute;
  bottom: -2px;
  width: 100%;
  border-bottom: 1px solid currentColor;
  transform: scaleX(0);
  transition-duration: 400ms;
}
@media (pointer: fine) {
  .link-decor:hover:before {
    transform: scaleX(1);
  }
}
.link--line {
  text-decoration: underline;
}

.list li {
  position: relative;
  padding-left: 0.15rem;
}
.list li:not(:last-child) {
  margin-bottom: 0.1rem;
}
.list li:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0.08rem;
  height: 1em;
  width: 1em;
  font-size: 0.08rem;
  border-radius: 50%;
  background-color: #000;
}

.logo {
  display: inline-block;
  flex-shrink: 0;
  line-height: 0;
}
.logo img {
  width: 0.99rem;
  height: sise(25);
}
@media (min-width: 1024px) {
  .logo img {
    width: 1.97rem;
    height: sise(50);
  }
}

.animation {
  animation-play-state: paused;
  animation-duration: 2s;
  animation-fill-mode: both;
}
.animation.start {
  animation-play-state: running;
}

@keyframes loading {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes loading-n {
  100% {
    transform: rotate(-720deg);
  }
}
.title {
