.form {
  display: flex;
  flex-direction: column;
}

.form-field {
  --dp-flex: flex;
  display: var(--dp-flex);
  flex-direction: column;
  position: relative;
  width: 100%;
  font-size: 0.16rem;
  font-weight: 500;
  --field-input-color: #353535;
  border-bottom: 1px solid rgb(44, 60, 68);
}
.form-field__input {
  color: var(--field-input-color);
  width: 100%;
  display: block;
  border: 0;
  transition: 0s;
}
.form-field__input::placeholder {
  opacity: 0;
}
.form-field__input:focus ~ .form-field__text, .form-field__input:not(:placeholder-shown) ~ .form-field__text {
  opacity: 0;
}
.form-field__input:-webkit-autofill {
  -webkit-text-fill-color: var(--field-input-color);
  transition: background-color 500000s ease-in-out 0s;
}
.form-field__error, .form-field__input, .form-field__required, .form-field__text {
  font-size: 1em;
  transition-duration: 400ms;
  padding: 0.14rem 0;
}
.form-field--phone .form-field__input,
.form-field--phone .form-field__error,
.form-field--phone .form-field__required,
.form-field--phone .form-field__text {
  padding-left: 0.3rem;
}
.form-field--phone:before {
  position: absolute;
  content: "";
  width: 0.19rem;
  height: 0.11rem;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  margin-top: 0.17rem;
  background-image: url("../svg-sprite/sprite.svg#flag");
  background-size: contain;
  background-position: center;
}
.form-field__error, .form-field__required, .form-field__text {
  position: absolute;
  left: 0;
  bottom: 0;
  pointer-events: none;
  width: 100%;
}
.form-field__text {
  color: #151515;
  opacity: 0.5;
}
.form-field__text span {
  color: #151515;
}
.form-field__error, .form-field__required {
  color: #151515;
  width: 100%;
  opacity: 0;
}
.form-field._required > *:not(.form-field__label) {
  opacity: 0;
}
.form-field._required .form-field__required {
  opacity: 0.5;
}
.form-field._error > *:not(.form-field__label) {
  opacity: 0;
}
.form-field._error .form-field__error {
  opacity: 0.5;
}
.form-field__label {
  font-size: 0.2rem;
  line-height: 1.2;
  margin-bottom: 0.1rem;
  text-transform: uppercase;
}

.form-file {
  position: relative;
  display: inline-flex;
  flex-direction: column;
  width: 100%;
}
.form-file__controller {
  display: flex;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #000;
  color: #fff;
  width: 100%;
  font-size: 0.24rem;
  height: 0.8rem;
  padding: 0 0.4rem;
  transition-duration: 400ms;
}
.form-file__controller:hover {
  transition-duration: 400ms;
}
.form-file__input {
  display: none;
}
.form-file__quantity {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  max-width: 0px;
  overflow: hidden;
  transition-duration: 100ms;
}
.form-file__quantity._active {
  padding-left: 15px;
  max-width: none;
}
.form-file__quantity._active .form-file__loader {
  opacity: 0;
  transition-delay: 375ms;
}
.form-file__quantity._active .form-file__number,
.form-file__quantity._active .form-file__icon {
  opacity: 1;
  transition-delay: 375ms;
}
.form-file__loader {
  position: absolute;
  z-index: 2;
  transition-duration: 150ms;
}
.form-file__number {
  margin-left: 6px;
  opacity: 0;
  transition-duration: 300ms;
}
.form-file__icon {
  opacity: 0;
  transition-duration: 300ms;
}

.form-textarea {
  position: relative;
  display: block;
  border: 1px solid rgb(185, 185, 185);
  transition-duration: 400ms;
}
.form-textarea__input {
  resize: none;
}
.form-textarea__input::placeholder {
  opacity: 0;
}
.form-textarea__input:focus ~ .form-textarea__text, .form-textarea__input:not(:placeholder-shown) ~ .form-textarea__text {
  opacity: 0;
}
.form-textarea__input, .form-textarea__required, .form-textarea__text {
  width: 100%;
  font-size: 1em;
  transition-duration: 400ms;
  padding: 0.25rem 0.4rem;
}
.form-textarea__required, .form-textarea__text {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2;
  pointer-events: none;
}
.form-textarea__text span {
  color: #4a928f;
}
.form-textarea__required {
