.menu-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 899;
  overflow: hidden;
  display: none;
}
.menu-modal__wrapper {
  display: flex;
  height: 100%;
  width: 100%;
  padding-top: 1.07rem;
  overflow-x: hidden;
  overflow-y: auto;
}
@media (min-width: 768px) {
  .menu-modal__wrapper {
    padding-top: 0.65rem;
  }
}
@media (min-width: 1024px) {
  .menu-modal__wrapper {
    padding-top: 0.7rem;
  }
}
.menu-modal__box {
  box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.1);
  background: rgb(249, 249, 249);
}
@media (min-width: 1024px) {
  .menu-modal__box {
    border-radius: 0px 0px 0.24rem 0.24rem;
  }
}
.menu-modal__item {
  padding: 0.2rem;
}
@media (min-width: 768px) {
  .menu-modal__item {
    padding: 0.4rem 1.25rem;
  }
}
.menu-modal__item:not(:last-child) {
  border-bottom: 1px solid rgb(144, 144, 144);
}
.menu-modal__content {
  display: flex;
  margin-left: -0.15rem;
  margin-right: -0.15rem;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .menu-modal__content {
    flex-direction: row;
  }
}
.menu-modal__content-info {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.08rem;
}
.menu-modal__content-info span {
  text-transform: uppercase;
  font-weight: 500;
  font-size: 0.12rem;
}
@media (min-width: 1024px) {
  .menu-modal__content-info span {
    font-size: 0.16rem;
  }
}
.menu-modal__content-info .icon {
  color: #a43e3d;
}
.menu-modal__content-info .icon--color-primary {
  color: #4a928f;
}
.menu-modal__content-item {
  text-decoration: none;
  padding-left: 0.15rem;
  padding-right: 0.15rem;
  width: 100%;
  margin-bottom: 0.2rem;
}
@media (min-width: 1024px) {
  .menu-modal__content-item {
    width: 33.3333%;
    margin-bottom: 0;
  }
}
.menu-modal__content-img {
  background-color: #fff;
  border-radius: 0.2rem;
  box-shadow: 0px 4px 8px 2px rgba(0, 0, 0, 0.1);
  margin-bottom: 0.15rem;
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 0;
}

.header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 950;
  box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.1);
  background-color: #f9f9f9;
}
.header._menu-active .header-hide {
  opacity: 0;
  pointer-events: none;
}
.header-hide {
  transition-duration: 400ms;
}
.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0.1rem;
  padding-top: 0.1rem;
  padding-bottom: 0.1rem;
  flex-wrap: wrap;
}
.header__btn {
  border: 1px solid #fdfdfd;
  box-shadow: 0 1px 2px 1px rgba(0, 0, 0, 0.1);
  border-radius: 2px;
}
.header__btn span {
  white-space: nowrap;
}
.header__contact {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  color: #522e18;
  width: 100%;
}
@media (min-width: 768px) {
  .header__contact {
    width: auto;
  }
}
.header__call {
  border-bottom: 1px dashed currentColor;
}

.footer {
  margin-top: auto;
  flex-shrink: 0;
}
.footer__ea {
